var net = require('net');

var maxPort = exports.maxPort = 65535;
var minUserPort = exports.minUserPort = 1024;

/**
 * @param  {Number|Object} options (allows for server.listen options)
 * @param  {function} callback
 */
var available = function (options, callback) {
  var numPort = typeof options === 'number' ? options : options.port;
  var server = net.createServer();
  var callbackCalled = false;

  server.on('error', function () {
    if (!callbackCalled) {
      callbackCalled = true;
      callback(null, false);
    }
  });
  server.listen(options, function () {
    server.close(function() {
      process.nextTick(function() {
        if (!callbackCalled) {
          callbackCalled = true;
          callback(null, numPort);
        }
      });
    });
  });
};

function arrayContains (arr, val) {
  for (var i = 0; i < arr.length; i++) {
    if (arr[i] == val) {
      return true;
    }
  }
  return false;
}

// options, callback
function find () {
  var callback;
  var options = {};
  if (typeof(arguments[0]) === 'function') {
    callback = arguments[0];
  } else {
    options = arguments[0];
    callback = arguments[1];
  }

  if (!callback) {
    throw new Error('callback is required');
  }

  options.startingPort = 'startingPort' in options ? options.startingPort : minUserPort;
  options.endingPort = options.endingPort || maxPort;
  options.ports = options.ports;
  options.count = options.count || 1;
  options.avoid = options.avoid || [];
  options.createServer = options.createServer || available;

  if (options.startingPort <= 0) {
    throw new Error("startingPort must be greater than 0");
  }
  if (options.endingPort > maxPort) {
    throw new Error("endingPort must be less than " + maxPort);
  }
  if (options.startingPort > options.endingPort) {
    throw new Error("startingPort must be less than or equal to endingPort");
  }

  var i = 0;
  var results = [];
  var loop = function () {
    var port;
    var host = options.host;
    if (options.ports) {
      if (i >= options.ports.length) {
        callback(new Error("no ports found"));
        return;
      }
      port = options.ports[i++];
    } else {
      port = options.startingPort++;
      if (port > options.endingPort) {
        callback(new Error("no ports found"));
        return;
      }
    }

    if (arrayContains(options.avoid, port)) {
      loop();
      return;
    }
    
    var serverOpts = port;
    if (host) {
      serverOpts = { host: host, port: port };
    }

    options.createServer(serverOpts, function (err, port) {
      if (!err && port) {
        results.push(port);
        if (results.length === options.count) {
          if (results.length === 1) {
            results = results[0];
          }
          callback(null, results);
          return;
        }
      }
      loop();
    });
  };
  loop();
}

exports.find = find;

